






























































	import uniPassword from '@/components/uni-password/uni-password.vue';
	import uniIcon from '@/components/i-icon/i-icon.vue';
	import uniKeyboard from '@/components/uni-keyboard.vue';
	import common from '../../common/common.js';
	import {
		mapState
	} from 'vuex';
	export default {
		components: {
			uniPassword,
			uniIcon,
			uniKeyboard
		},
		data() {
			return {
				tabCurrentIndex: 0,
				swiperCurrentIndex: 0,
				titleShowId: 'tabTag-0',
				tabHeight: 300,
				tabs: [
					//标签名称 , 分类 id , 加载更多, 加载的页码
					{
						name: '登陆密码',
						id: 'pwd1',
						loadingType: 0,
						page: 1
					},
					{
						name: '二级密码',
						id: 'pwd2',
						loadingType: 0,
						page: 1
					}
				],
				showKeyboard: false
			}
		},
		onLoad() {

		},
		computed: {
			...mapState(['hasLogin', 'userInfo', 'bi'])
		},
		methods: {
			done(password) {
				console.log(password);
				console.log(this.$refs);
			},
			confirmPwd() {
				this.$refs.keyboard.show();
			},
			onInput(e) {
				//e.cancel = true;
				let password = e.value;
				console.log(password)
			},
			onConfirm(e) {
				let password = e.value;
				let that = this;
				console.log(password)
				var url = common.pwdEdit;
				var old_pwd = that.userInfo.pwd1;
				if (that.titleShowId == 'tabTag-1') {
 old_pwd = that.userInfo.pwd2;
					url = common.pwd2Edit;
				}
				console.log(url)

				uni.request({
					url: url, //仅为示例，并非真实接口地址。
					data: {
						is_mobile: 1,
						user_id: that.userInfo.id,
						pwd: password,
						repwd: password,
						old_pwd:old_pwd

					},
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: function(res) {
						if (res.data.status == 1) {

							that.$api.msg(res.data.info);
							setTimeout(() => {
								uni.navigateBack()
							}, 800)
						} else {

							that.$api.msg(res.data.info);

						}



					}
				});
			},
			tabChange(e) {
				var index = e.target.id.replace('tabTag-', '');
				this.swiperCurrentIndex = index;
				this.tabCurrentIndex = index;
				this.titleShowId = 'tabTag-' + index;
			},
			swiperChange: function(e) {
				var index = e.detail.current;
				this.tabCurrentIndex = index;
				this.titleShowId = 'tabTag-' + index;
			}
		},
		onReady() {
			//获取屏幕高度及比例
			var winInfo = uni.getSystemInfo({
				success: res => {
					var windowHeight = res.windowHeight;
					//获取头部标题高度
					var dom = uni.createSelectorQuery().select('#tab-title');
					dom.fields({
						size: true
					}, res2 => {
						if (!res2) {
							return;
						}
						//计算得出滚动区域的高度
						this.tabHeight = windowHeight - res2.height;
						console.log(this.tabHeight);
					}).exec();
				}
			});
		}
	}
